<template>
  <div>
    <transition name="el-fade-in-linear">
      <div v-show="show" class="v-main-container">
        <h1 class="marry">我要结婚了</h1>
        <nav-bar></nav-bar>
      </div>
    </transition>
  </div>
</template>

<script>
import navBar from '../../components/nav/navBar'
export default {
  name: 'introMain',
  components: {
    navBar
  },
  mounted () {
    this.show = true
  },
  data () {
    return {
      show: false
    }
  }
}
</script>

<style scoped lang="scss">

@import "../../style/entry";
.marry{
  width: 100%;
  font-size: .6rem;
  text-align: center;
  margin-top: 5rem;
}

</style>



